<template>
  <section>

    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item label="手机号">
					<el-input v-model="filters.phone" ></el-input>
				</el-form-item>
        <el-form-item label="用户名">
					<el-input v-model="filters.name" ></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button @click="handleClean">清空</el-button>
				</el-form-item>
			</el-form>
		</el-col>


    <!--列表-->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<!-- <el-table-column type="selection" width="55"> -->
			<!-- </el-table-column> -->
			<el-table-column prop="recordId" label="recordId" width="120" sortable>
			</el-table-column>
			<el-table-column prop="user.name" label="姓名" width="120">
			</el-table-column>
			<el-table-column prop="user.phone" label="手机" width="140">
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间" sortable :formatter="timeFormat">
			</el-table-column>
			<el-table-column prop="interal" label="积分" min-width="180" sortable>
      </el-table-column>
    </el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

  </section>
</template>

<script>
  import fetch from '../../common/js/fetch'
	import moment from 'moment'

  export default {
    data() {
      return {
        page: 1,
        pageSize: 20,
        listLoading: false,
        total: 20,
        query: {
          name: '',
          way: '充值'
        },
        users: [],
        filters: {
          phone: '',
          name: ''
        }
      }
    },
    methods: {
      handleCurrentChange() {

      },
      timeFormat(row, column, cellValue){
				return moment(cellValue).format('YYYY-MM-DD hh:mm:ss')
      },
      getUsers() {

				const phoneReg = /^1\d{10}$/

        const {
          phone,
          name 
        } = this.filters

        if(this.filters.phone && !phoneReg.test(this.filters.phone)){
					return this.handleError('请输入合法手机号')
				}

        let userDynamic = `${phone ? `phone='${phone}'`: 'phone>0'}
                          ${name ? `&& name='${name}'` : ''}`
        userDynamic = userDynamic.replace(/\s/g, '')

        let param = {
          page: this.page,
          pageSize: this.pageSize,
          userDynamic
        }

        fetch('/record/list?', {param}, (data,err)=>{
          if(err){return console.log(err)}

          this.users = data
        })
      },
      handleClean() {
        Object.keys(this.filters).map(item => {
          this.filters[item] = ''
        })
      },
      selsChange: function (sels) {
				this.sels = sels;
      },
      handleError: function(msg){
        this.$message({
						showClose: true,
						message: msg,
						type: 'error'
        });
      },
      getCount() {
        fetch('/record/list/count')
          .then(data=>{
            this.total = data
          })
          .catch(err=>{
            console.log(err)
          })
      }
    },
    mounted() {
      this.getUsers()
    }
  }
</script>

<style>

</style>




